import styled from 'styled-components';

const StyledNav = styled.nav`
  display: flex;
  justify-content: space-between;
  align-items: center;
`;

const Title = styled.h2`
  font-size: 2em;
`;

type NavbarProps = {
  title: string;
};

const Navbar: React.FunctionComponent<NavbarProps> = ({ title }) => (
  <StyledNav>
    <Title>{title}</Title>
  </StyledNav>
);

export default Navbar;
